a{
  color: #efe;
  text-decoration: none;
}

a:visited{
  color: #afe;
}

.actionZone{
  display: grid;
  grid-template-columns: repeat(2,50%);
  height: 90%;
  width: 100%;
  z-index: 4;
}

.BuscadorStyles{
  background-color: #8eb7f76e;
  backdrop-filter: blur(4px);
  border-radius: 10px;
  align-self: center;
  justify-self: center;
  height: 50%;
  width: 80%;
}

.rootStyles{
  display: grid;
  grid-template-rows: 8vh 92vh;
  background: #000214;
  color: #efe;
  height: 100vh;
  width: 100%;
}

.headerContainer{
  display: flex;
  justify-content: space-between;
  background-color: #8eb7f76e;
  backdrop-filter: blur(4px);
  width: 100%;
}

.rrssList{
  display: flex;
  align-items: center;
  list-style: none;
  height: 8vh;
  width: 10%;
}

.rrssList > li{
  margin-right: .5rem;
}

.squaretostyle{
  background-color: #181818;
  backdrop-filter: blur(2px);
  height: 50vh;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
  border-top: 10px solid #475068;
}

.lyricZone{
  align-self: center;
  justify-self: center;
  height: 90%;
  width: 90%;
}

@media screen and (max-width: 600px){
  .actionZone{
    grid-template-columns: 100%;
    grid-template-rows: 40% 60%;
    height: 95%;
  }

  .BuscadorStyles{
    height: 90%;
  }

  .lyricZone{
    max-width: 500px;
  }
}