.zone {
  background-color: #d6eefd66;
  backdrop-filter: blur(4px);
  border-radius: 10px;
  height: 100%;
  max-height: 500px;
  padding: 1rem;
  width: 95%;
  overflow-y: scroll;
  justify-self: center;
}

.zone::-webkit-scrollbar{
  width: 1rem;
}

.zone::-webkit-scrollbar-track{
  background: #d6eefd66;
  margin-block: .5rem;
  border-radius: 10px;
}

.zone::-webkit-scrollbar-thumb{
  background: #6495ed;
  border-radius: 10px;
}

.zone::-webkit-scrollbar-thumb:hover{
  background: #42619b;
}

@media screen and (max-width:600px){
  .zone{
    width: 100%;
    max-width: 500px;
    padding:0;
  }
}