.form{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
  width: 80%;
  margin: 0 auto;
}

.form > *{
  border-radius: 10px;
  border: none;
  font-weight: bolder;
  padding: .5rem 1rem;
  /* margin-left: 1rem; */
  outline: none;
}

.form > p{
  font-size: 1.2rem;
}

.form input{
  height: 2.5rem;
  transition: .5s ease;
  box-shadow: 0 10px 30px 1px #0000004f;
}

.form input[type="submit"]{
  background-color: cornflowerblue;
  cursor: pointer;
  transition: .5s ease;
}

.form input[type="submit"]:hover{
  background-color: rgba(100, 148, 237, 0.486);
  backdrop-filter: blur(5px);
}

.form input[type="text"]:focus::placeholder{
  color: rgb(0, 0, 0);
  font-size: .8rem;
  transition: all .5s ease;
}

.form input[type="text"]:focus::placeholder{
  color: rgb(146, 146, 146);
  font-size: .8rem;
}